<template>
  <div>
    <Loadings v-if="list ==null"></Loadings>
    <div class="container scontainer" v-else>
      <div class="search-bar">
        <van-search
          v-model="keywords"
          show-action
          label="查询"
          placeholder="请输入搜索关键词"
          @search="searchData"
          clearable
        >
          <template #action>
            <div @click="searchData">搜索</div>
          </template>
        </van-search>
      </div>
      <Empty v-if=" list.length == 0"></Empty>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        class="list"
        v-else
      >
        <div
     
          class="item"
          v-for="(item, index) in list"
          :key="index"
        >
          <div class="info">
            <div class="title flex-sb">
              {{item.company}}
              <span class="time">2020-9-8</span>
            </div>
            <div>合同号: {{item.cust_no}}</div>
            <div>客户电话: {{item.phone}}</div>
            <div>业务员: {{item.sname}}</div>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import { order } from "@api/public";
import { Icon, Search, Loading, List } from "vant";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
export default {
  name: "Product",
  components: {
    [Icon.name]: Icon,
    [Search.name]: Search,
    [Loading.name]: Loading,
    [List.name]: List,
    Empty,
    Loadings,
  },

  data: function () {
    return {
      list: [
        {
          cid: "13178",
          name: " 黄S",
          company: "深圳市锦龙自动化设备有限公司",
          call: "",
          total_sum: "580",
          address: "深圳市宝安区石岩街道北环路华丰圳宝工业园5栋2楼东",
          total_order: "1",
          phone: "15818870229",
          cust_no: "A866B58409464088998F792B44A2FDA7",
          staff_no: "A03",
          create_time: "1514908800",
          type: null,
          status: "合作中客户",
          visiting_time: "0",
          visiting_date: null,
          sname: "付中",
          department: "业务部",
        },
        {
          cid: "12375",
          name: "刘韩芬（崔小姐）",
          company: "JAESHIN.CO",
          call: "",
          total_sum: "1420",
          address:
            "广州市白云区\n广园中路203-207号景泰西2巷2号102档（河北大厦后座）广州正鸿国际物流有限公司",
          total_order: "1",
          phone: "13825077605",
          cust_no: "B81B1783C7BE48759060FA667FF2EFF4",
          staff_no: "A03",
          create_time: "1537459200",
          type: null,
          status: "合作中客户",
          visiting_time: "0",
          visiting_date: null,
          sname: "付中",
          department: "业务部",
        },
      ],
      keywords: "",
      page: "",
      loading: false,
      finished: true,
    };
  },
  created() {},
  mounted: function () {
    this.cust_no = this.$route.params.type;

  },
  methods: {
    searchData() {
      if (this.keywords != "") {
        this.page = "";
      }
  
    },
    formatDate: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
    toThousands(num) {
      var result = [],
        counter = 0;
      num = (num || 0).toString().split("");
      for (var i = num.length - 1; i >= 0; i--) {
        counter++;
        result.unshift(num[i]);
        if (!(counter % 3) && i != 0) {
          result.unshift(",");
        }
      }
      return result.join("");
    },
    getData() {
      var data = {
        search: this.keywords,
        page: this.page,
      };
      if (this.cust_no != -1) {
        data.cust_no = this.cust_no;
      }
      order(data).then((res) => {
        if (this.page != "") {
          this.list = this.list.concat(res.data.order);
        } else {
          this.list = res.data.order;
        }

        this.finished = res.data.order.length < 200 ? true : false;
        this.loading = res.data.order.length < 200 ? true : false;
        if (res.data.order.length == 200) {
          this.page = this.list[this.list.length - 1].oid;
        }
      });
    },
    onLoad() {
      this.getData();
    },
  },
};
</script>
<style scoped>
.container {
  background: #fff;
}

.container .list {
  line-height: 43px;
  font-size: 14px;
}

.container .list .item {
  display: block;
  padding: 15px 8px;

  border-bottom: 10px solid #f9f9f9;
}
.title {
  margin-bottom: 5px;
  color: #333;
  font-size: 14px;
}
.title span {
  font-size: 12px;
  color: #999;
}
.container .list .item div {
  width: 100%;
}

.container .list .info {
  line-height: 18px;
  font-size: 12px;
  color: #999;
}

.container .list .item .right {
  padding-right: 15px;
  font-weight: bold;
}

.container .list .item .right span {
  font-size: 10px;
  font-weight: 500;
}

.company {
  color: #333;
  font-size: 16px;
}

.contact {
  color: #999;
  margin: 5px 0;
  font-size: 12px;
}

.contact span {
  padding-left: 10px;
}

.address {
  color: #999;
  font-size: 12px;
  margin-bottom: 5px;
}

.list img {
  width: 30px;
}

.loading-tips {
  text-align: center;
  padding-top: 150px;
}
.info .price {
  font-size: 12px;
  color: #999;
}
</style>
